#pechiko-list{
    position: absolute;
//    top: 356px;
//    left: 174px;
    @include itemLayout(712,355);
    z-index: 9;
    .pechiko-item{
        position: absolute;
        top: 0;
        left: 0;
        z-index: 20;
        @for $i from 1 through 6 {
            &.pechiko-item-#{$i}{
                @include itemLayout(38 * ($i) ,68 * ($i))
            }
        }
        @for $i from 1 through 6 {
            &.pechiko-item-#{$i + 5}{
                @include itemLayout((38 * ($i)) - 70 ,68 * ($i))
            }
        }
        @for $i from 1 through 6 {
            &.pechiko-item-#{$i + 11}{
                @include itemLayout(38 * ($i) ,(-68 * ($i)) + 790 )
            }
        }
        @for $i from 1 through 6 {
            &.pechiko-item-#{$i + 17}{
                @include itemLayout((38 * ($i)) - 50 ,(-68 * ($i)) + 710 )
            }
        }


    }
}


/*スプライトアニメーション*/
.pechiko{
    position: absolute;
    width: 60px;
    height: 60px;
    &.man-bike{
        background-image: url('../images/man-bike.png');
        &.bike1{
            animation: sprite1 0.5s steps(7, start) infinite;
        }
        &.bike2{
            animation: sprite2 0.5s steps(7, start) infinite;
        }
        &.bike3{
            animation: sprite3 0.5s steps(7, start) infinite;
        }
        &.bike4{
            animation: sprite4 0.5s steps(7, start) infinite;
        }
        &.bike5{
            transform: rotateY(180deg);
            animation: sprite1 0.5s steps(7, start) infinite;
        }
        &.bike6{
            transform: rotateY(180deg);
            animation: sprite2 0.5s steps(7, start) infinite;
        }
        &.bike7{
            transform: rotateY(180deg);
            animation: sprite3 0.5s steps(7, start) infinite;
        }
        &.bike8{
            transform: rotateY(180deg);
            animation: sprite4 0.5s steps(7, start) infinite;
        }
    }
    &.man-bike-down{
        background-image: url('../images/man-bike-down.png');
        &.bike1{
            animation: sprite5 0.5s steps(10, start);
        }
        &.bike2{
            animation: sprite6 0.5s steps(10, start);
        }
        &.bike3{
            animation: sprite7 0.5s steps(10, start);
        }
        &.bike4{
            animation: sprite8 0.5s steps(10, start);
        }
        &.bike5{
            transform: rotateY(180deg);
            animation: sprite5 0.5s steps(10, start);
        }
        &.bike6{
            transform: rotateY(180deg);
            animation: sprite6 0.5s steps(10, start);
        }
        &.bike7{
            transform: rotateY(180deg);
            animation: sprite7 0.5s steps(10, start);
        }
        &.bike8{
            transform: rotateY(180deg);
            animation: sprite8 0.5s steps(10, start);
        }
    }
    &.clap{
        background-image: url('../images/man-clap.png');
        &.bike1{
            animation: sprite9 0.5s steps(4, start);
        }
        &.bike2{
            animation: sprite10 0.5s steps(4, start) infinite;
        }
        &.bike3{
            animation: sprite11 0.5s steps(4, start) infinite;
        }
        &.bike4{
            animation: sprite12 0.5s steps(4, start) infinite;
        }
        &.bike5{
            transform: rotateY(180deg);
            animation: sprite9 0.5s steps(4, start) infinite;
        }
        &.bike6{
            transform: rotateY(180deg);
            animation: sprite10 0.5s steps(4, start) infinite;
        }
        &.bike7{
            transform: rotateY(180deg);
            animation: sprite11 0.5s steps(4, start) infinite;
        }
        &.bike8{
            transform: rotateY(180deg);
            animation: sprite12 0.5s steps(4, start) infinite;
        }
    }
}

//自転車で走るアニメーション sprite1-4

@keyframes sprite1 {
    0%   {background-position: 0px 420px;}
    100% {background-position: 0px 0px;}
}

@keyframes sprite2 {
    0%   {background-position: 64px 420px;}
    100% {background-position: 64px 0px;}
}

@keyframes sprite3 {
    0%   {background-position: 180px 420px;}
    100% {background-position: 180px 0px;}
}

@keyframes sprite4 {
    0%   {background-position: 360px 420px;}
    100% {background-position: 360px 0px;}
}

//自転車を降りるアニメーション sprite 5-8

@keyframes sprite5 {
    0%   {background-position: 0px 600px;}
    100% {background-position: 0px 0px;}
}

@keyframes sprite6 {
    0%   {background-position: 64px 600px;}
    100% {background-position: 64px 0px;}
}

@keyframes sprite7 {
    0%   {background-position: 180px 600px;}
    100% {background-position: 180px 0px;}
}

@keyframes sprite8 {
    0%   {background-position: 360px 600px;}
    100% {background-position: 360px 0px;}
}

//拍手アニメーション sprite  9-12

@keyframes sprite9 {
    0%   {background-position: 0px 240px;}
    100% {background-position: 0px 0px;}
}

@keyframes sprite10 {
    0%   {background-position: 64px 240px;}
    100% {background-position: 64px 0px;}
}

@keyframes sprite11 {
    0%   {background-position: 180px 240px;}
    100% {background-position: 180px 0px;}
}

@keyframes sprite12 {
    0%   {background-position: 360px 240px;}
    100% {background-position: 360px 0px;}
}

