.button{
    &.button-about{
        position: fixed;
        bottom: 70px;
        right: 30px;
        @include sizeCtl(306,40);
        text-decoration :none;
        cursor: pointer;
        z-index: 21;
    }
    .button-text{
        color: #bbeeed;
        @extend %font-teko;
        position: absolute;
        z-index: 2;
        top: 3px;
        left: 10px;
        font-size: 18px;
        letter-spacing: 0.06em;

    }
    .button-bg{
        display: block;
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        @include sizeCtl(306,40);
        background: url(../images/bg_about.png);
        background-size: 100% auto;
    }
}