.about-box{
    text-align: center;
    position: fixed;
    top :0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 20;
    overflow-y: scroll;
    svg{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
    }
    .about-inner{
        position: relative;
        z-index: 3;
//        opacity: 0;
            background-color: #b2d452;
    }
    .logo{
        padding-top: 145px;
    }
    .about{
        color: #FFFFFF;
        .title{
            @include rem(25);
            font-weight: bold;
        }
        .text{
            @include rem(16);
            margin-top: 40px;
            line-height: 30px;
            font-weight: bold;
        }
        .about-body{

        }
        &.about01{
            padding: 42px 0 120px;

        }
        &.about02{
            letter-spacing: 0.08em;
            padding: 98px 0;
            background-color: #a1c048;
        }
    }
}